let fs = 14;
let lh = 1.5;
let rlh = fs * lh;
let ts = {
  textTransform: 'uppercase',
};

let Key = item => {
  let [key, label] = item;
  return (
    <div
      style={{
        marginRight: '1ch',
        display: 'flex',
        marginBottom: rlh / 4,
      }}
    >
      <div
        style={{
          border: 'solid 1px black',
          paddingLeft: '0.5ch',
          paddingRight: '0.5ch',
        }}
      >
        {key}
      </div>
      <div
        style={{
          paddingLeft: '1ch',
          paddingRight: '1ch',
          borderTop: 'dotted 1px white',
          borderRight: 'dotted 1px white',
          borderBottom: 'dotted 1px white',
        }}
      >
        {label}
      </div>
    </div>
  );
};

let KeyList = ({ items }) => {
  return (
    <div style={{ display: 'flex' }}>
      {items.map(item => {
        return Key(item);
      })}
    </div>
  );
};

let Relevant = ({ mode }) => {
  switch (mode) {
    case 'move':
      return (
        <div>
          <div
            style={{
              marginBottom: rlh / 4,
            }}
          >
            <div style={ts}>Move</div>
            <KeyList items={[['h', '←'], ['j', '↓'], ['k', '↑'], ['l', '→']]} />
            <KeyList items={[['shift', 'hold to move by 10']]} />
          </div>
          <div
            style={{
              marginBottom: rlh / 4,
            }}
          >
            <div style={ts}>Action</div>
            <KeyList
              items={[['enter', 'select flow'], ['2', 'adjust cursor']]}
            />
          </div>
        </div>
      );
    default:
      return null;
  }
};

export default ({ rlh }) => {
  return (
    <div
      style={{
        border: 'solid 1px black',
        paddingRight: '1ch',
        paddingLeft: '1ch',
        paddingTop: rlh / 4,
        paddingBottom: rlh / 4,
        position: 'fixed',
        outline: color === 'dark' ? 'solid 1px white' : 'solid 1px black',
        maxWidth: `calc(100% - 4ch)`,
        background: 'white',
        paddingLeft: '2ch',
        paddingRight: '2ch',
        paddingBottom: rlh / 2,
        paddingTop: rlh / 2,
        right: '2ch',
        bottom: rlh,
        overflowY: 'auto',
      }}
    >
      <Relevant mode="move" />
      <div style={ts}>Special</div>
      <KeyList items={[['a', 'west'], ['w', 'north']]} />
    </div>
  );
};
